import React, { useContext, useState, useRef } from 'react'
import { TaskContext, ModalContext } from './Contexts'
import TaskForm from './TaskForm'
import { DATETIME_OPTIONS, shown_dateTime } from './DateTimeUtils'
import Icon from './Icon'
import Draggable from 'react-draggable';

export default function ItemInList({children}) {
    // console.log('render item in list')
    // console.log(t)

    const modal = useContext(ModalContext)
    const task = useContext(TaskContext)

    const [isDragging, setIsDragging] = useState(false)
    const nodeRef = useRef(null)

    const handleClick = () => {
        if (isDragging) {
            console.log(`is dragging`)
            return
        }
        modal.setContent(
            <TaskForm oldTask={task} handleCloseForm={()=>{modal.setContent(null)}}/>
        )
    }

    const bg_while_dragging = ' bg-secondary'

    return (
        <Draggable
            axis='x'
            onDrag={()=>{
                console.log('onDrag')
                setIsDragging(true)
            }}
            onStop={()=>{
                console.log('onStop')
                setTimeout(() => {
                    setIsDragging(false);
                  }, 50)
            }}
            nodeRef={nodeRef}
            >
                <div ref={nodeRef} className={
                    'task-in-list d-flex align-items-start p-3' + (isDragging? bg_while_dragging : '')
                    } onClick={handleClick}>
                    {children}
                </div>
        </Draggable>
    )
}

export const ListItemCheckbox = () => {
    const task = useContext(TaskContext)
    return (
        <div className='me-3 align-self-center'>
            { task.doneDate? <Icon i='bi-check-square-fill'/> : <Icon i='bi-square'/> }
        </div>
    )
}

export const ListItemName = () => {
    const task = useContext(TaskContext)
    return (
        <div className={ ` lh-sm fs-5 me-auto `+ decorate_by_priority(task.priority) }>
            {task.taskName}
        </div>
    )
}

export const ListItemDateTime = ({options=DATETIME_OPTIONS.FULL}) => {
    const task = useContext(TaskContext)
    return (
        <div className='flex-shrink-0 text-body-tertiary'>
            {shown_dateTime(task.dueDate, options)}
        </div>
    )
}

export const decorate_by_priority = (p) => {
    const high = 'text-info'
    const medium = ''
    const low = 'text-body-tertiary'
    if (p >= 6) {
        return high
    }
    else if (p >= 3) {
        return medium
    }
    else {
        return low
    }
}